import React from 'react'
import {useState, useEffect} from 'react'
import {useNavigate} from 'react-router-dom'
import axios from 'axios'

export default function UserHome() {

    const navigate= useNavigate()

    const [clientInfo, setClientInfo] = useState(null)
    const username = localStorage.getItem('username')

    useEffect(() => {
        console.log(username)
        axios.get(`https://osmolearn-dev.herokuapp.com:5000/accounts/${username}`).then(res => {
            console.log(res.data)
            setClientInfo(res.data)
            localStorage.setItem('isTutor', res.data.isTutor)
        }).catch(err => console.error(err))
        
    }, [])

    return (
        <div>
            {clientInfo && clientInfo.upcomingSessions.map(session => 
                <div key={Math.floor(Math.random() * 100)}>
                    <p>SessionId: {session.sessionId}</p>
                    <button onClick={e => navigate(`/session/${session.sessionId}`)}>Join Session</button>
                    <br/>
                </div>)}
        </div>
    )
}
