import styles from './TutoringRoom.module.css'
import {useState, useEffect, useRef} from 'react'
import axios from 'axios'
import Peer from 'peerjs';
import {io} from 'socket.io-client'
import {ReactSketchCanvas} from 'react-sketch-canvas';
import {MdScreenShare,MdStopScreenShare,MdOutlineColorLens,MdOutlineVideocam,MdOutlineVideocamOff} from 'react-icons/md'
import {GrRedo,GrUndo} from 'react-icons/gr'
import {BsEraser,BsPencilFill,BsMicMute,BsMic, BsChatDots} from 'react-icons/bs'
import Timer from '../../Components/Timer/Timer';
import ReactTooltip from 'react-tooltip';
import {GithubPicker} from 'react-color'
import Chat from '../../Components/Chat/Chat';

export default function TutoringRoom(props) {
    
    // const [clientId, setClientId] = useState(null)
    const [streamMyVideo, setStreamMyVideo] = useState(true)
    const [myVideoTrackStopped, setMyVideoTrackStopped] = useState(false)
    const [streamMyAudio, setStreamMyAudio] = useState(true)
    const [myAudioTrackStopped, setMyAudioTrackStopped] = useState(false)
    const [showOtherClientVideo, setShowOtherClientVideo] = useState(false)
    const [allPartiesJoined, setAllPartiesJoined] = useState(false)
    const [socketEventQueue, setSocketEventQueue] = useState([])
    const [showTimer, setShowTimer] = useState(false)
    const [isSharingScreen, setIsSharingScreen] = useState(false)
    const [otherClientScreenSharing, setOtherClientScreenSharing] = useState(false)
    const [showColorPicker, setShowColorPicker] = useState(false)
    const [penColor, setPenColor] = useState('black')
    const [messages, setMessages] = useState([])
    const [myMessage, setMyMessage] = useState('')

    const isTutor = localStorage.getItem('isTutor')
    const clientName = localStorage.getItem('username')
    const latentVideoDisable = useRef([])
    const canvasRef = useRef()
    const socket = useRef(null)
    const peer = useRef(null)
    const screenPeer = useRef(null)
    const myVideo = useRef()
    const keyRefs = useRef({})
    const otherClientName = useRef(null)
    const otherClientVideo = useRef()
    const myScreen = useRef()
    const otherClientScreen = useRef()

    const callClient = () => {
        var getUserMedia = navigator.mozGetUserMedia || navigator.mediaDevices.mozGetUserMedia || navigator.mediaDevices.getUserMedia || navigator.mediaDevices.webkitGetUserMedia;
        getUserMedia({ video: true, audio: true }).then( (stream) => {
            console.log("calling: ",otherClientName.current)
            let call = peer.current.call(otherClientName.current, stream)
            call.on('stream', remoteStream => {
                setShowOtherClientVideo(true)
                otherClientVideo.current.srcObject = remoteStream
            })
        }).catch(err => console.error(err))
    }

    const traceSketch = canvasPaths => {
        if(socket.current){
            allPartiesJoined ? socket.current.emit('draw', {sessionId:"07b799f3-8e4d-4c26-a4bd-d65b80ce83db", canvasPaths: canvasPaths}) : 
                               setSocketEventQueue(prev => [...prev, {eventName: 'draw', data: {sessionId:"07b799f3-8e4d-4c26-a4bd-d65b80ce83db", canvasPaths: canvasPaths}}])
        }
    }

    const handleShareScreen = () => {
        console.log("sharing screen")
        if(!isSharingScreen){
            if(!screenPeer.current){
                //If screenPeer wasn't already made before. This prevents the constant recreation of screen peer when reattempting to screenshare
                const shareScreenPeer = new Peer(`${clientName}-video`, {
                    host: 'osmolearn-dev.herokuapp.com',
                    port: '3001',
                    path: '/call'
                })
                screenPeer.current = shareScreenPeer
                shareScreenPeer.on("open", id => {
                    shareScreenPeer.on("call", call => {
                        call.answer(null) //not going to provide a stream back in this case
                        call.on("stream", screen => {
                            //Handle latent action by disabling the ability to screenshare when both parties aren't in (easier on me and server load)
                            setOtherClientScreenSharing(true)
                            otherClientScreen.current.srcObject = screen
                            console.log("answered screenshare call and got screenshare data")
                        })
                    })
                })
                socket.current.emit('screenshare', {sessionId: "07b799f3-8e4d-4c26-a4bd-d65b80ce83db"}) //emitted only for first time sharing screen
            }
            navigator.mediaDevices.getDisplayMedia({video: {cursor: "always"}, audio: true})
                                    .then(screen => {
                                        screen.getVideoTracks()[0].onended = () => {
                                            setIsSharingScreen(false)
                                            // myScreen.current.srcObject.getTracks().forEach(track => track.stop())
                                            socket.current.emit('end screenshare', {sessionId: "07b799f3-8e4d-4c26-a4bd-d65b80ce83db"}) //used for setting states
                                            if(screenPeer.current){
                                                screenPeer.current.destroy()
                                                screenPeer.current = null
                                            }
                                            myScreen.current = null
                                        }
                                        screenPeer.current.call(`${otherClientName.current}-video`, screen)
                                        console.log(`called ${otherClientName.current}-video`)
                                        setIsSharingScreen(true)
                                        myScreen.current.srcObject = screen
                                    }).catch(err => console.error(err))
        } else {
            //Shut down share screen and destroy screenPeer connection and set screenShare to null
            setIsSharingScreen(false)
            myScreen.current.srcObject.getTracks().forEach(track => track.stop())
            socket.current.emit('end screenshare', {sessionId: "07b799f3-8e4d-4c26-a4bd-d65b80ce83db"}) //used for setting states
            if(screenPeer.current){
                screenPeer.current.destroy()
                screenPeer.current = null
            }
            myScreen.current = null
        }
    }

    const loadKeyDownHandlers = () => {
        window.addEventListener('keydown', e => {
            if(e.key === 'Control' || e.key === 'z' || e.key === 'x'){
                keyRefs.current[e.key] = e.key
            }
            if(Object.keys(keyRefs.current).length >= 2){
                if(keyRefs.current['Control'] && keyRefs.current['z']){
                    canvasRef.current.undo()
                    allPartiesJoined ? socket.current.emit('undo', {sessionId: "07b799f3-8e4d-4c26-a4bd-d65b80ce83db"}):
                           setSocketEventQueue(prev => [...prev, {eventName: 'undo', data: {sessionId: "07b799f3-8e4d-4c26-a4bd-d65b80ce83db"}}])

                }
                if(keyRefs.current['Control'] && keyRefs.current['x']){
                    canvasRef.current.redo()
                    allPartiesJoined ? socket.current.emit('redo', {sessionId: "07b799f3-8e4d-4c26-a4bd-d65b80ce83db"}):
                           setSocketEventQueue(prev => [...prev, {eventName: 'redo', data: {sessionId: "07b799f3-8e4d-4c26-a4bd-d65b80ce83db"}}])
                }
            }
        })

        window.addEventListener('keyup', e => {
            keyRefs.current = {}
        })
    }

    const undoDrawing = () =>{
        canvasRef.current.undo()
        allPartiesJoined ? socket.current.emit('undo', {sessionId: "07b799f3-8e4d-4c26-a4bd-d65b80ce83db"}):
                           setSocketEventQueue(prev => [...prev, {eventName: 'undo', data: {sessionId: "07b799f3-8e4d-4c26-a4bd-d65b80ce83db"}}])
    }

    const redoDrawing = () =>{
        canvasRef.current.redo()
        allPartiesJoined ? socket.current.emit('redo', {sessionId: "07b799f3-8e4d-4c26-a4bd-d65b80ce83db"}):
                           setSocketEventQueue(prev => [...prev, {eventName: 'redo', data: {sessionId: "07b799f3-8e4d-4c26-a4bd-d65b80ce83db"}}])
    }

    const changePenColor = (color, event) => {
        console.log('color change to:', color.hex)
        setPenColor(color.hex)
    }

    const sendMessage = () => {
        console.log("message:", myMessage)
        setMessages(prev => [...prev, {sender: clientName, message: myMessage}])
        allPartiesJoined ? socket.current.emit('message', {sessionId: "07b799f3-8e4d-4c26-a4bd-d65b80ce83db", message: myMessage}):
                           setSocketEventQueue(prev => [...prev, {eventName: 'message', data: {sessionId: "07b799f3-8e4d-4c26-a4bd-d65b80ce83db", message: myMessage}}])
        setMyMessage('')
    }

    useEffect(() => {

        loadKeyDownHandlers()
        socket.current = io("http://osmolearn-dev.herokuapp.com:5001/")
        socket.current.emit('user connected', {sessionId:"07b799f3-8e4d-4c26-a4bd-d65b80ce83db", name: clientName})

        socket.current.on('disable other client video', _ => {
            console.log("other client stopped their video feed")
            if(otherClientVideo.current.srcObject){
                otherClientVideo.current.srcObject.getVideoTracks().forEach(track => track.enabled = false);
            } else {
                latentVideoDisable.current.push('video')
            }
        })
        socket.current.on('enable other client video', _ => {
            console.log("other client enabled their video feed")
            if(otherClientVideo.current.srcObject){
                otherClientVideo.current.srcObject.getVideoTracks().forEach(track => track.enabled = true);
            }
        })
        socket.current.on('disable other client audio', _ => {
            console.log("other client stopped their audio feed")
            if(otherClientVideo.current.srcObject){
                otherClientVideo.current.srcObject.getAudioTracks().forEach(track => track.enabled = false);
            }else {
                latentVideoDisable.current.push('audio')
            }
        })
        socket.current.on('enable other client audio', _ => {
            console.log("other client enabled their audio feed")
            if(otherClientVideo.current.srcObject){
                otherClientVideo.current.srcObject.getAudioTracks().forEach(track => track.enabled = true);
            }
        })
        socket.current.on('draw', canvasPaths => {
            canvasRef.current.loadPaths(canvasPaths)
        })

        socket.current.on('screenshare', () => {
            console.log(`Getting screenshare data`)
            if(!screenPeer.current){
                //If screenPeer wasn't already made before. This prevents the constant recreation of screen peer when reattempting to screenshare
                const shareScreenPeer = new Peer(`${clientName}-video`, {
                    host: 'osmolearn-dev.herokuapp.com',
                    port: '3001',
                    path: '/call'
                })
                screenPeer.current = shareScreenPeer
                shareScreenPeer.on("open", id => {
                    shareScreenPeer.on("call", call => {
                        call.answer(null) //not going to provide a stream back in this case
                        call.on("stream", screen => {
                            //Handle latent action by disabling the ability to screenshare when both parties aren't in (easier on me and server load)
                            setOtherClientScreenSharing(true)
                            otherClientScreen.current.srcObject = screen
                            console.log("answered screenshare call and got screenshare data")
                        })
                    })
                })
            }
        })

        socket.current.on('end screenshare', () => {
            if(screenPeer.current){
                screenPeer.current.destroy()
                screenPeer.current = null
            }
            setOtherClientScreenSharing(false)
            otherClientScreen.current = null
        })

        socket.current.on('undo', () => {
            canvasRef.current.undo()
        })

        socket.current.on('redo', () => {
            canvasRef.current.redo()
        })

        socket.current.on('message', message => {
            console.log(message)
            setMessages(prev => [...prev, {sender: otherClientName.current, message: message}]) //when I send message sender will be me
        })

        axios.get(`https://osmolearn-dev.herokuapp.com:5000/call/${clientName}/07b799f3-8e4d-4c26-a4bd-d65b80ce83db`).then(res=> otherClientName.current = res.data)
        .catch(err => console.error(err))

        var getUserMedia = navigator.mozGetUserMedia || navigator.mediaDevices.mozGetUserMedia || navigator.mediaDevices.getUserMedia || navigator.mediaDevices.webkitGetUserMedia;
        getUserMedia({ video: true, audio: true }).then(stream => {
            myVideo.current.srcObject = stream;
            myVideo.current.play();
        }).catch(err => console.error(err))

        if(!peer.current) {
            const peerConnection = new Peer(clientName, {
                host: 'osmolearn-dev.herokuapp.com',
                port: '3001',
                path: '/call'
            })
            peer.current = peerConnection
            peerConnection.on("open", id => {
                console.log(`${id} connected`)
                // setClientId(id)
                peerConnection.on("call", call => {
                    console.log("getting a call")
                    var getUserMedia = navigator.mozGetUserMedia || navigator.mediaDevices.mozGetUserMedia || navigator.mediaDevices.getUserMedia || navigator.mediaDevices.webkitGetUserMedia ;
                    getUserMedia({ video: true, audio: true }).then((stream) => {
                        call.answer(stream)
    
                        call.on('stream', remoteStream => {
                            setShowOtherClientVideo(true)
                            otherClientVideo.current.srcObject = remoteStream
                            if(latentVideoDisable.current.includes('video')){
                                console.log('latent disabling client video')
                                otherClientVideo.current.srcObject.getVideoTracks().forEach(track => track.enabled = false);
                            }
                            if(latentVideoDisable.current.includes('audio')){
                                console.log('latent disabling client audio')
                                otherClientVideo.current.srcObject.getAudioTracks().forEach(track => track.enabled = false);
                            }
                        })
                    }).catch(err => console.error(err))
                })
            })
        }
        console.log(peer.current)
        socket.current.on('all clients connected', data => {
            setAllPartiesJoined(true)
            setShowTimer(true)
            console.log("all clients connected")
            if(isTutor === "true"){
                callClient(true)
            }
        })
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    //Handles disabling my own video
    useEffect(() => {
        //disable my video and send signal to other client to disable their video
        if(myVideo.current !== null && !streamMyVideo){
            myVideo.current.srcObject.getVideoTracks().forEach(track => track.enabled = !track.enabled);
            allPartiesJoined? socket.current.emit('disable video', {sessionId:"07b799f3-8e4d-4c26-a4bd-d65b80ce83db", disable: true}) :
                              setSocketEventQueue(prev => [...prev, {eventName: 'disable video' , data: {sessionId:"07b799f3-8e4d-4c26-a4bd-d65b80ce83db", disable: true}}])
            setMyVideoTrackStopped(true)
        }
        //enable my video and send signal to other client to enable their video
        if(myVideo.current !== null && streamMyVideo && myVideoTrackStopped){
            myVideo.current.srcObject.getVideoTracks().forEach(track => track.enabled = !track.enabled);
            allPartiesJoined? socket.current.emit('disable video', {sessionId:"07b799f3-8e4d-4c26-a4bd-d65b80ce83db", disable: false}) :
                              setSocketEventQueue(prev => [...prev, {eventName: 'disable video' , data: {sessionId:"07b799f3-8e4d-4c26-a4bd-d65b80ce83db", disable: false}}])
            setStreamMyVideo(true)
            setMyVideoTrackStopped(false)
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [streamMyVideo])

    //Handles disabling my own video
    useEffect(() => {
        //disable my video and send signal to other client to disable their video
        if(myVideo.current !== null && !streamMyAudio){
            allPartiesJoined? socket.current.emit('disable audio', {sessionId:"07b799f3-8e4d-4c26-a4bd-d65b80ce83db", disable: true}) :
                              setSocketEventQueue(prev => [...prev, {eventName: 'disable audio' , data: {sessionId:"07b799f3-8e4d-4c26-a4bd-d65b80ce83db", disable: true}}])
            setMyAudioTrackStopped(true)
        }
        //enable my video and send signal to other client to enable their video
        if(myVideo.current !== null && streamMyAudio && myAudioTrackStopped){
            allPartiesJoined? socket.current.emit('disable audio', {sessionId:"07b799f3-8e4d-4c26-a4bd-d65b80ce83db", disable: false}) :
                              setSocketEventQueue(prev => [...prev, {eventName: 'disable audio' , data: {sessionId:"07b799f3-8e4d-4c26-a4bd-d65b80ce83db", disable: false}}])
            setStreamMyAudio(true)
            setMyAudioTrackStopped(false)
        }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [streamMyAudio])

    useEffect(() => {
        socketEventQueue.forEach(event => {
            socket.current.emit(event.eventName, event.data)
        })
        //reset event queue in case one party leaves (will write code to emit signal when user leaves abruptly to properly set AllPartiesJoined to false)
        setSocketEventQueue([])
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [allPartiesJoined])

    return (
        <div className={styles.roomContainer}>
            <div className={styles.videoFeedsContainer}>
                <div className={styles.myVideoContainer}>
                    <video className={styles.videoFeed} muted ref={myVideo}/>
                    <div>
                        <div onClick={e => {setStreamMyAudio(!streamMyAudio)}} className={styles.muteButton}>
                            {streamMyAudio? <BsMic size={30}/> : <BsMicMute size={30}/>}
                        </div>
                        <div onClick={e => {setStreamMyVideo(!streamMyVideo)}} className={styles.stopVideoButton}>
                            {streamMyVideo? <MdOutlineVideocam size={30}/> : <MdOutlineVideocamOff size={30}/>}
                        </div>
                        <div onClick={e => allPartiesJoined? handleShareScreen() : alert("Can't share screen until both parties join")} className={styles.screenShareBtn}>
                            {isSharingScreen? <MdStopScreenShare className={styles.screenShareIcon} fontSize={30}/> : <MdScreenShare className={styles.screenShareIcon} fontSize={30}/>}
                        </div>
                    </div>
                    <p className={styles.name}>{clientName}</p>
                </div>
                <div style={{display: showOtherClientVideo? 'block' : 'none'}} className={styles.otherClientVideoContainer}>
                    <video className={styles.videoFeed} autoPlay ref={otherClientVideo}/>
                    <p className={styles.otherClientName}>{otherClientName.current}</p>
                </div>
                {isSharingScreen && 
                <div className={styles.myScreenContainer}>
                    <video className={styles.videoFeed} autoPlay ref={myScreen}/>
                </div>}
                {showTimer && <div className={styles.timer}><Timer/></div>}
            </div>
            <div className={styles.canvas}>
                <ReactSketchCanvas
                height={window.innerHeight}
                strokeWidth={5}
                strokeColor={penColor}
                ref={canvasRef}
                onStroke={traceSketch}
                />
            </div>
            {otherClientScreenSharing && 
            <div className={styles.otherScreenContainer}>
                <video className={styles.otherClientScreenVid} autoPlay ref={otherClientScreen}/>
            </div>}
            <div className={styles.tools}>
                <div onClick={undoDrawing} data-delay-show='500' data-tip="undo" data-for="undoTip" className={styles.tool}>
                    <GrUndo size={20}/>
                </div>
                <div onClick={redoDrawing} data-delay-show='500' data-tip="redo" data-for="redoTip" className={styles.tool}>
                    <GrRedo size={20}/>
                </div>
                <div onClick={e => setShowColorPicker(!showColorPicker)} className={styles.tool}>
                    <MdOutlineColorLens data-delay-show='500' data-tip="color" data-for="colorTip" size={20}/>
                    {showColorPicker && <GithubPicker className={styles.colorPicker} onChange={changePenColor} presetColors={['#D0021B', '#F5A623', '#F8E71C', '#8B572A', '#7ED321', '#417505', '#BD10E0', '#9013FE', '#4A90E2', '#50E3C2', '#B8E986', '#000000', '#4A4A4A', '#9B9B9B', '#FFFFFF']}/>}
                </div>
                <div data-delay-show='500' data-tip="eraser" data-for="eraserTip" className={styles.tool}>
                    <BsEraser size={20}/>
                </div>
                <div data-delay-show='500' data-tip="pencil" data-for="pencilTip" className={styles.tool}>
                    <BsPencilFill size={20}/>
                </div>
                <ReactTooltip place='right' id="undoTip">
                    Undo
                </ReactTooltip>
                <ReactTooltip place='right' id="redoTip">
                    Redo
                </ReactTooltip>
                <ReactTooltip place='right' id="colorTip">
                    Change Pen Color
                </ReactTooltip>
                <ReactTooltip place='right' id="eraserTip">
                    Eraser
                </ReactTooltip>
                <ReactTooltip place='right' id="pencilTip">
                    Pencil
                </ReactTooltip>
            </div>
            <Chat clientName={clientName} messages={messages} myMessage={myMessage} setMyMessage={setMyMessage} sendMessage={sendMessage}/>
            <div className={styles.chatBtn}>
                Chat &nbsp; <BsChatDots fontSize={20}/>
            </div>
        </div>
    )
}