import styles from './Chat.module.css'
import {MdSend} from 'react-icons/md'
import {useEffect} from 'react'

export default function Chat({clientName, messages, myMessage, setMyMessage, sendMessage}) {

    useEffect(() => {
        console.log(messages)   
    })

    return (
        <div className={styles.chat}>
            <div className={styles.messagesContainer}>
                {messages.map(message => {
                    console.log(message)
                    return (
                        <>
                        <div className={message.sender === clientName? styles.messageContainer: styles.otherMessageContainer}>
                            <div className={styles.sender}>{message.sender}</div>
                            <div key={message.message[0] + message.message[message.message.length - 1]} className={message.sender === clientName? styles.myMessage : styles.otherClientMessage}>
                                <div className={styles.message}>{message.message}</div>
                            </div>
                        </div>
                        </>
                    )
                })}
            </div>
            <div className={styles.messageInputContainer}>
                <input className={styles.messageInput} type="text" placeholder="Type a message here..." value={myMessage} onChange={e => setMyMessage(e.target.value)}/>
                <div onClick={sendMessage} className={styles.sendMessageBtn}>
                    <MdSend className={styles.sendIcon} fontSize={25}/>
                </div>
            </div>
        </div>
    )
}
