import styles from './Timer.module.css'
import {useStopwatch} from 'react-timer-hook'

export default function Timer() {
    const {
        seconds,
        minutes,
        hours
      } = useStopwatch({autoStart: true})
    return (
        <div className={styles.timer}>
            <p>{hours}:{minutes}:{seconds}</p>
        </div>
    )
}
